            <h3 class="mb-0">Archive Teachers</h3>
          </div>
        </div>
      </div>

      <el-table 
        class="table-responsive align-items-center table-flush tk-table-max-height"
        header-row-class-name="thead-light" :data="archive">
        <el-table-column label="Name" min-width="200px" prop="name" sortable>
          <template v-slot="{ row }">
            <img
              :src="row.image"
              class="avatar rounded-circle mr-3 tk-cursor-pointer"
              @click.prevent="preview(row)"
            />
            <b
              class="tk-layout-inline-block tk-text-overflow tk-cursor-pointer"
              style="width: calc(100% - 64px)"
              @click.prevent="preview(row)"
              >{{ row.name }}</b
            >
          </template>
        </el-table-column>

