<script>
import bus from "@/bus";
import users from "./../teachers";
import archive from "./../archive";
import {
  Table,
  TableColumn,
  DropdownMenu,
  DropdownItem,
  Dropdown,
  Tooltip,
} from "element-ui";

export default {
  name: "inline-actions-table",
  components: {
    [Tooltip.name]: Tooltip,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    [Dropdown.name]: Dropdown,
    [DropdownItem.name]: DropdownItem,
    [DropdownMenu.name]: DropdownMenu,
  },
  data() {
    return {
      users,
      archive,
      currentPage: 1,
      currentPage2: 1,
      isInvalid: false,
      deleteArchive: false,
      fullName: "",
      phone: "",
      email: "",
      url: "",
      index: -1,
    };
  },
  mounted() {
    let self = this
    bus.$on('onActive', data => {
      self.onActive(data)
      console.log(self.archive);
    })
    bus.$on('onArchive', data => {
      self.onArchive(data)
    })
    bus.$on('confirmDelete', data => {
      if (data.active) {
        self.onDelete(data)
      }else {
        self.onDeleteArchive(data)
      }
      self.confirmDelete()
    })
  },
  methods: {
    preview(data) {
      this.$emit("preview", data)
    },
    onDelete(row) {
      let self = this;
      self.users.some((item, i) => {
        if (item.id === row.id) {
          self.index = i;
        }
      });
    },
    onDeleteArchive(row) {
      let self = this;
      self.archive.some((item, i) => {
        if (item.id === row.id) {
          self.index = i;
          self.deleteArchive = true;
        }
      });
    },
    confirmDelete() {
      let self = this;
      if (self.deleteArchive) {
        self.archive.splice(self.index, 1);
        self.deleteArchive = false;
      } else {
        self.users.splice(self.index, 1);
      }
      self.index = -1;
    },
    onArchive(row) {
      // alert(`You want to archive ${row.name}`);
      let self = this;
      self.users.some((item, i) => {
        if (item.id === row.id) {
          self.users.splice(i, 1);
          self.archive.push(row);
        }
      });
    },
    onActive(row) {
      // alert(`You want to archive ${row.name}`);
      let self = this;
      self.archive.some((item, i) => {
        if (item.id === row.id) {
          self.archive.splice(i, 1);
          self.users.push(row);
        }
      });
    },
  },
};
</script>
